.tvShow {
    position: relative;
    margin: 1%;
    cursor: pointer;
}

.info {
    position: absolute;
    bottom: 2px;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.65);
    overflow: hidden;
    width: 100%;
    height: 50px;
    transition: 1s ease;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}

.tvShow:hover .info {
    height: 60%;
}

.showTitle {
    font-weight: bold;
    font-size: 20px;
    color:white;
    padding: 15px;
    padding-top: 0px;
    margin-top: 3px;
}

.infoBody {
    color:white;
    padding-left: 10px;
    margin: 0;
}

.infoFooter {
    color:white;
    padding-left: 10px;
    font-size: 13px;
}

.noFooter {
    display: none;
}

.showPic {
    width: 250px;
    height: 375px;
    border-radius: 25px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.recoPic {
    width: 180px;
    height: 270px;
    border-radius: 25px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.starRatings {
    unicode-bidi: bidi-override;
    color: rgb(163, 141, 141);
    font-size: 20px;
    height: 20px;
    width: 100px;
    margin-left: 10px;
    margin-bottom: 30px;
    position: relative;
    padding: 0;
    text-shadow: 0px 1px 0 #a2a2a2;
}

.starRatingsTop {
    color: #f7cc0c;
    padding: 0;
    position: absolute;
    z-index: 1;
    display: block;
    top: 0;
    left: 0;
    overflow: hidden;
}

.starRatingsBottom {
    padding: 0;
    display: block;
    z-index: 0;
}

/*
Star Ratings css code from https://codepen.io/Bluetidepro/pen/GkpEa
By Zach Reed
Changes made were width, height, and color
*/

@media (max-width: 550px)
{

    .showPic {
        width: 180px;
        height: 270px;
    }

    .info {
        display: none;
    }

}

@media (max-width: 379px) 
{

    .showPic {
        width: 165px;
        height: 247px;
    }

}

@media (max-width: 349px)
{

    .showPic {
        width: 150px;
        height: 225px;
    }
 
}

@media (max-width: 319px)
{

    .showPic {
        width: 250px;
        height: 375px;
    }

}

@media (max-width: 259px)
{

    .showPic {
        width: 180px;
        height: 270px;
    }

}